                loop: false, // 导致视频一结束就重新开始。
                preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
                language: 'zh-CN',
                aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
                fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
                sources: [{
                    type: "",
                    src: "http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4" //视频url地址
                }],
                poster: "", //你的封面地址
                // width: document.documentElement.clientWidth,
                notSupportedMessage: '此视频暂无法播放，请稍后再试', //允许覆盖Video.js无法播放媒体源时显示的默认信息。
                controlBar: {
                    timeDivider: true,
                    durationDisplay: true,
                    remainingTimeDisplay: false,
                    fullscreenToggle: true  //全屏按钮
                }
            },
            reason:'',//不合格原因
        }
    },
    methods:{
        openVisible(e){
            this.index1 = e
            this.dialogVisible = true
        },
        openVisible2(){
            this.dialogVisible2 = true
        },
        openVisible3(){
            this.dialogVisible3 = true
        },
        openVisible4(){
            if(this.reason == ""){
                this.$message({
                    type: 'info',
                    message: '请选择不合格原因'
                });
            }else{
                this.dialogVisible4 = false
                this.reason = ''
            }
        },
        openVisible6(){
            this.dialogVisible4 = false
            this.reason = ''
        },
        openVisible5(){
            this.$confirm('确定审核通过, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
                this.$message({
                    type: 'success',
                    message: '操作成功!'
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消操作'
                });          
            });
        },
        setTimer(){
            // 请求数据
            // this.timer = setInterval(()=>{
            //     this.examineData.push({a:1})
            // }, 5000)
        }
    },
    mounted(){
        Vue.buttonAudio = document.getElementById('music')
        Vue.buttonAudio.pause()
        this.examineData = this.examineDatas[0]
        
    },
    destroyed(){
        clearInterval(this.timer)
    },
    watch:{
        'examineDatas.length':{
            handler:(val, olVal)=>{
                if(olVal === 0){
                    Vue.buttonAudio.play()
                }
            }
        },
        dialogVisible5: function(newVal){
            if (newVal == false) {
            this.$refs.videoPlayer.player.pause()
            }else{
            this.$refs.videoPlayer.player.play()
            }
        }
    }
}
